<template>
  <div class="orders">
    <div class="item-img" @click="toDetail">
      <img :src="order.ogimg" alt="商品图片">
    </div>


    <div class="item-info">
      <div class="item-title">{{order.ogtitle}}</div>

      <div class="item-centr">
        <div class="item-desc">商品大小: {{order.ogsize}}</div>
        <div class="item-count">商品数量：{{order.ogcount}}个</div>
      </div>

      <div class="item-bottom">
        <div class="item-time">{{getTime}}</div>
        
      </div>
    </div>


    <div class="item-right">
      <div class="item-price">¥{{order.ogprice}}</div>
      <div class="itemb" @click="changeStatus" v-if="isShow">确认收货</div>
      <div class="itemb1" v-else>交易成功</div>
    </div>
  </div>
</template>

<script>
  import {formatDate} from 'commons/utils'
  import {updOrderstatus} from 'network/order'
  export default {
    name: 'OrdersItem',
    props: {
      order: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      getTime() {
        //转换时间
        let date = new Date(this.order.otime*1000);
        // console.log(date.toLocaleString().replace(new RegExp('/','g'),"-"))
        return formatDate(date)
      },
      //判断显示收货还是确认收货
      isShow() {
        if(this.order.ogstatus == "未收货") {
          return true
        }else {
          return false
        }
      }
    },
    methods: {
       toDetail() {
        this.$router.push('/detail/' + this.order.gid)
      },
      //确认收货
      changeStatus() {
        //发送请求修改订单属性
        updOrderstatus(this.order.oid).then(res => {
          if(res == 1) {
            this.$toast.show('收货成功')
            //将这个订单移动到已收货数组中  发送事件
            this.$emit('updOstatus', this.order.oid)
          }
        })
        

      }
    }
  }
</script>

<style scoped>
  .orders{
    width: 100%;
    display: flex;
    padding: 5px;
    border-bottom: 1px solid #ccc;
  }
  .item-img {
    padding: 5px;
    /*border: 1px solid #ccc;*/
  }

  .item-img img {
    width: 80px;
    height: 100px;
    display: block;
    border-radius: 5px;
  }
  .item-info {
    font-size: 14px;
    color: #333;
    padding: 5px 10px;
    position: relative;
    overflow: hidden;
    width: 50%;
  }
  .item-title{
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .item-desc,.item-count{
    margin-bottom: 5px;
  }
  .item-bottom{
    margin-top: 10px;
  }
  .item-time{
    font-size: 10px;
  }
  .item-right{
    padding: 10px;
  }
  .item-price{
    color: red;
    text-align: center;
    margin-top: 10px;
  }
  .itemb{
    font-size: 11px;
    background-color: forestgreen;
    height: 30px;
    width: 70px;
    margin: auto;
    line-height: 25px;
    border-radius: 30px;
    margin-top: 35px;
    text-align: center;
  }
  .itemb1{
    font-size: 11px;
    height: 30px;
    width: 70px;
    margin: auto;
    line-height: 25px;
    border-radius: 30px;
    margin-top: 35px;
    text-align: center;
  }
</style>