<template>
   <div class="oo">
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
        <img src="~assets/img/back.svg" alt="">
      </div>
      <div slot="center" class="nav-titles">我的订单</div>
    </nav-bar>
    <tab-control :titles="tabCon" class="tabcon"  @tabClick="tabClick"></tab-control>
    <!--展示订单信息-->
    <scroll class="scrollo">
      <orders-list :orders="showOrders" @updOstatus="updOstatus"></orders-list>
    </scroll>
    
   </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import TabControl from 'content/tabcontrol/TabControl'
  import OrdersList from './childcomps/OrdersList'
  import Scroll from 'common/scroll/Scroll'
  import {getUserOrders} from 'network/order'
  export default {
    name: 'Order',
    components: {
        NavBar,
        TabControl,
        OrdersList,
        Scroll
    },
    data() {
      return {
        //用来保存订单的信息
        noGoods: [],
        haveGoods: [],
        //保存导航栏的信息
        isTab: 0
      }
    },
    created () {
      //发送请求获取订单信息 根据uid 默认获取未收货订单    和已收货订单
      getUserOrders(this.getUserInfo).then(res => {
        this.noGoods = res[0];
        this.haveGoods = res[1];
      })
    },
    computed: {
      //获取用户信息
      getUserInfo() {
        return this.$store.state.userInfo.uid
      },
      tabCon() {
        let a = '未收货（' + this.noGoods.length + ')';
        let b = '已收货（' + this.haveGoods.length + ')';
        return [a,b]
      },
      //默认展示的订单
      showOrders() {
        if(this.isTab == 0) {
          return this.noGoods
        } else {
          return this.haveGoods
        }
      }
    },
    methods: {
      backClick() {
        this.$router.back()
      },
      tabClick(index) {
        if(index == 0) {
          this.isTab = 0
        } else {
          this.isTab = 1
        }
      },
      updOstatus(oid) {
        //根据id将未收货的商品放入已收货中
        for(let i = 0;i<this.noGoods.length;i++) {
          if(this.noGoods[i].oid == oid) {
            //将该对象添加到已收货中
            this.haveGoods.push(this.noGoods[i])
            //在数组中删除该对象
            this.noGoods.splice(i,1);
          }
        }
      }
    }
  }
</script>

<style scoped>
  .oo{
    height: 100vh;
  }
  .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .scrollo{
    height: calc(100% - 93px - 40px);
    overflow: hidden;
  }
</style>