<template>
  <div>
    <orders-item v-for="(item, index) of orders" :key="index" :order="item" @updOstatus="updOstatus"></orders-item>
  </div>
</template>

<script>
  import OrdersItem from './OrdersItem'
  export default {
    name: 'OrdersList',
    components: {
      OrdersItem
    },
    props: {
      orders: {
        type: Array,
        default() {
          return []
        }
      }
    },
    methods: {
      updOstatus(oid) {
        this.$emit('updOstatus', oid)
      }
    }
  }
</script>

